const displayMediaOptions = {
	video: {
		cursor: 'never',
		displaySurface: "monitor",
	},
	audio: false,
};

type TCaptureStream = MediaStream;
type TStopCaptureStream = () => void;

type TGetCaptureStream = () => Promise<[
	TCaptureStream,
	TStopCaptureStream,
]>;

const getCaptureStream: TGetCaptureStream = async () => {
	// @ts-ignore
    const captureStream = await navigator.mediaDevices.getDisplayMedia(displayMediaOptions);

    const stopCaptureStream: TStopCaptureStream = () => {
        if (captureStream) {
            captureStream.getTracks()[0].stop();
        }
    };

    return [
        captureStream,
        stopCaptureStream,
    ];
};

export default getCaptureStream;
