import React, { useCallback, useEffect } from 'react';

import {
    Button,
    createImageElement,
    readFileAsDataURL,
    useSpin,
} from '@brz/ui';

import screenCapture from '../../utils/screenCapture';

import { TImg } from '../../state/draw/types/types';

import LayoutMain from '../../componeents/LayoutMain';

import useGoToPage from '../../hooks/useGoToPage';
import useClipboardImageFile from '../../hooks/useClipboardImageFile';
import useDrawInit from '../../hooks/useDrawInit';
import useBeep from '../../hooks/useBeep';

import styles from './style.module.css';

const Home = () => {
    const { beepError } = useBeep();

    const clipboardImageFile = useClipboardImageFile();

    const [initFromClipboardSpin, setInitFromClipboardSpin] = useSpin(false);
    const [startCaptureSpin, setStartCaptureSpin] = useSpin(false);

    const drawInit = useDrawInit();
    const goToPage = useGoToPage();

    const initFromClipboard = useCallback(async (imageFile: File) => {
        setInitFromClipboardSpin(true);
        try {
            const dataUrl = await readFileAsDataURL(imageFile);
            const imageElement = await createImageElement(dataUrl);
            const data: TImg = {
                dataUrl,
                width: imageElement.naturalWidth,
                height: imageElement.naturalHeight,
            };
            drawInit(data);
            goToPage('/draw');
        } catch (error) {
            beepError(error);
        }
        setInitFromClipboardSpin(false);
    }, [
        drawInit,
        goToPage,
        setInitFromClipboardSpin,
        beepError,
    ]);

    useEffect(() => {
        if (clipboardImageFile) {
            initFromClipboard(clipboardImageFile);
        }
    }, [
        clipboardImageFile,
        initFromClipboard,
    ]);

    const startCapture = useCallback(async () => {
        setStartCaptureSpin(true);
        try {
            const data = await screenCapture();
            drawInit(data);
            goToPage('/draw');
        } catch (error) {
            beepError(error);
        }
        setStartCaptureSpin(false);
    }, [
        goToPage,
        drawInit,
        setStartCaptureSpin,
        beepError,
    ]);

    const loading = initFromClipboardSpin || startCaptureSpin;

    return (
        <LayoutMain loading={loading}>
            <div className={styles.root}>

                <div className={styles.logo}>screen-capture</div>

                <h1 className={styles.h1}>Простой инструмент сделать и поделиться скриншотом.</h1>

                <ol className={styles.features}>
                    <li>Универсальный инструмент доступный из браузера.</li>
                    <li>Возмоность сделать скриншот с помощью опереционной системы или захватить экран с помощью "Screen Capture API".</li>
                    <li>Возмоность кадрировать и редактировать скриншот.</li>
                    <li>Возможность делиться отредактированным скриншотом через буфер обмена или сохраняя как файл.</li>
                </ol>

                <ul className={styles.steps}>
                    <li>
                        <div>Скопировать экран в буфер обмена<br />(Windows: <b>&laquo;Print Screen&raquo;</b>)</div>
                        <div>Вставить экран из буфера обмена<br />(Windows: <b>&laquo;Ctrl + v&raquo;</b>)</div>
                    </li>
                    <li>
                        <Button
                            text="Захват экрана"
                            onClick={startCapture}
                        />
                    </li>
                </ul>
            </div>
        </LayoutMain>
    );
};

export default Home;
