type TData = {
	videoElement: CanvasImageSource;
	width: number;
	height: number;
};

type TGetVideoElementData = (mediaStream: MediaStream) => Promise<TData>;

const getVideoElementData: TGetVideoElementData = (mediaStream) => {
	return new Promise((resolve, _reject) => {
		const videoElement = document.createElement("video");

		videoElement.style.cssText = "position:absolute;left:0;top:0;";

		videoElement.autoplay = true;

		videoElement.onloadedmetadata = () => {
			const width = videoElement.videoWidth;
			const height = videoElement.videoHeight;

			videoElement.style.width = width + "px";
			videoElement.style.height = height + "px";

			resolve({
				videoElement,
				width,
				height
			});
		};

		videoElement.srcObject = mediaStream;
	});
};

export default getVideoElementData;
