import { useEffect, useState } from 'react';

import {
    getImageFileFromClipboard,
    useBeep,
    useIsMounted,
} from '@brz/ui';

type TFileData = File | null;
type TUse = () => TFileData;

const useClipboardImageFile: TUse = () => {

    const beep = useBeep();

    const [fileData, setFileData] = useState<TFileData>(null);

    const isMounted = useIsMounted();

    useEffect(() => {
        document.onpaste = async (event: ClipboardEvent) => {
            if (event.clipboardData) {
                const items: DataTransferItemList = event.clipboardData.items;
                const file: TFileData = getImageFileFromClipboard(items);
                if (file !== null) {
                    if (isMounted()) {
                        setFileData(file);
                    }
                } else {
                    beep.info('Пусто!');
                }
            }
        };
        return () => {
            document.onpaste = null;
        };
    }, [
        isMounted,
        beep,
    ]);

    return fileData;
};

export default useClipboardImageFile;
