const imageFormat = 'image/png';

type TProps = {
	videoElement: CanvasImageSource;
	width: number;
	height: number;
};

type TCreateDataURL = (props: TProps) => string;

const createDataURL: TCreateDataURL = (props) => {
	const {
		videoElement,
		width,
		height,
	} = props;

	const canvas = document.createElement("canvas");

	canvas.width = width;
	canvas.height = height;

	const canvasRenderingContext2D = canvas.getContext("2d");
	if (canvasRenderingContext2D === null) {
		throw new Error("Error canvas getContext 2d");
	}
	canvasRenderingContext2D.drawImage(videoElement, 0, 0, width, height);
	const src = canvas.toDataURL(imageFormat);

	canvas.remove();

	return src;
};

export default createDataURL;
