import createDataURL from "./createDataURL";
import getCaptureStream from "./getCaptureStream";
import getVideoElementData from "./getVideoElementData";

type TData = {
    dataUrl: string;
    width: number;
    height: number;
};

type TScreenCapture = () => Promise<TData>;

const screenCapture: TScreenCapture = async () => {
    const [captureStream, stopCaptureStream] = await getCaptureStream();

    const { videoElement, width, height } = await getVideoElementData(captureStream);

    const dataUrl = createDataURL({ videoElement, width, height });

    const data = {
        dataUrl,
        width,
        height,
    };

    stopCaptureStream();

    return data;
};

export default screenCapture;
